@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Yeseva+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
    box-sizing: border-box;
    background-color: #f6e8ea;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

.wave {
    background-image: url(/static/media/wave.59c925cd.svg);
    background-size: cover;
    height: '100vh';
    min-height:'100vh';
    margin-bottom: 15vh;
}

.main-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    margin-top: 6%; 
}

.hi {
    font-size: 7vh;
}

.title {
    padding-right: 1%;
    text-align: end;
    font-weight: 500;
    font-size: 4vh;
    font-family: 'Josefin Sans';
}

.point {
    display: none;
    opacity: 0;
    transition: 1s opacity;
}

.title:hover {
    cursor: pointer;
}

.title:hover .point{
    display: inline;
    cursor: pointer;
    opacity: 1;
    -webkit-animation: rotateit 1.3s linear 1;
     animation: rotateit 1.3s linear 1;
}

@-webkit-keyframes rotateit {
    from {
         -webkit-transform: rotate(360deg);
    }
    to {
         -webkit-transform: rotate(0deg);
    }
}
@keyframes rotateit {
    from {
         -webkit-transform: rotate(360deg);
                 transform: rotate(360deg);
    }
    to {
         -webkit-transform: rotate(0deg);
                 transform: rotate(0deg);
    }
}

.cartoon {
    background-image: url(/static/media/rabbit_hole.c5a77d03.png);
    background-size: cover;
    width: 62vh;
    height: 75vh;
    display: flex;
    align-self: center;
    padding-left: 1%;
}

.arrow-wrap {
    display: flex;
    justify-content: center;
}

.down-arrow {
    background-image: url(data:image/png;base64,);
    background-size: cover;
    width: 2rem;
    height: 2rem;
    margin: 3rem;
}

@-webkit-keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}
.nav-wrap {
    display: flex;
    justify-content: space-between;
    padding: .5rem 2.5rem 0 0;
}

.nav-list {
    width: 50%;
    font-family: 'Josefin Sans';
    font-weight: 400;
    font-size: 3vh;
    margin: 0;
    padding: .2vh;
    display: flex;
    justify-content: space-between;
    align-content: center;
    top: 0;
    list-style: none;
}


li, a {
    color: black;
    text-decoration: none;
    padding: .3rem;
}

.nav-list li {
    transition: 1s box-shadow;
    border-radius: 10px;
}

.nav-list li:hover{
    background-color: #f6e8ea;
    cursor: pointer;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.sticky {
    margin-left: 1rem;
}

.icon-wrap-nav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    position: fixed;
}

.nav-icons {
    width: 4.5vh;
    height: 4.5vh;
    padding-right: 1vh;
}

.nav-icons:hover {
    opacity: 50%;
}
#work {
    margin-left: 2vh;
}

.work-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.project-divider-wrap {
    display: flex;
    flex-direction: row;
    width: 100%;
    overflow: hidden;
}

.rep-title-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.rep-icon {
    width: 10vh;
    height: 10vh;
    align-self: center;
    margin-right: 1rem;
}



.cards {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    list-style: none;
}

.card-item {
    display: flex;
    width: 40%;
    margin: 5%;
    padding: 0;
    box-shadow: 0 0 20px rgba(0,0,0,0.2);
}

.work-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.image-paragraph-wrap {
    display: flex;
    justify-content: center;
}

.title-para-button-wrapping {
    flex-wrap: wrap;
    margin: 1rem;
    width: 60%;
    text-align: center;
}

.type-icon {
    width: 15%;
    height: auto;
    margin: 0;
}

.main-image-photo-wrap {
    max-height: 70vh;
    max-width: 75vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    margin: 5%;
}

.project-head-photo {
    width: 100%;
    align-self: flex-start;
}

.work-button-wrapping {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: flex-end;
}

.work-button {
    background-color: #f2c885;
    color: black;
    font-family: 'Josefin Sans';
    font-size: 2.5vh;
    font-weight: bold;
    padding: .7rem;
    margin: 1rem;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    background-image: url(/static/media/button_wave.7ec7c171.svg);
    background-size: cover;
}

.work-button:hover, .popup-button:hover {
    background-color: #fcefaa;
    cursor: pointer;
    box-shadow: none;
}

.work-paragraph {
    font-size: 3vh;
    font-family: 'Josefin Sans';
}

.video-wrap {
    width: 100%;
    display: flex;
    justify-content: center;
}


.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0,0,0, 0.5);
    overflow: scroll;
    font-family: 'Josefin Sans';
}

.popup-wrap {
    position: absolute;
    text-align: center;
    left: 14%;
    right: 14%;
    top: 11%;
    bottom: 11%;
    margin: auto;
    min-width: 262px;
    padding-bottom: 3rem;
    overflow: scroll;
    background-color: #f6e8ea;
}

.button-wrap {
    width: 92%;
    text-align: right;
}

.popup-button {
    background-color: #f2c885;
    color: black;
    font-size: 1rem;
    margin: 1rem;
    padding: .5rem;
    /* align-self: center; */
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    background-image: url(/static/media/button_wave.7ec7c171.svg);
    background-size: cover;
    font-weight: bold;
    z-index: 25;
    position: fixed;
    text-align: right;
}

.title-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.head-photo .modal-image {
    width: 55%;
    margin: 1rem;
    padding: 0;
}

.modal-image {
    margin-bottom: 4rem;
    position: relative;
    text-align: center;
}

.modal-image:hover {
    /* opacity: 80%; */
    cursor: pointer;
}

.modal-image:hover .image-writing{
    opacity: 100%;
}

.image-writing {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 3vh;
    font-weight: bold;
    color: black;
    opacity: 0;
    border: solid black 2px;
    background-color: #f6e8ea;
    border-radius: 20px;
    padding: 1rem;
}

.main-side-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.side-info, .side-info ul, .main-work-wrap, .sidebar-wrap, .about-work-wrap {
    margin: .5rem;
    padding: 0;
    display: flex;
    flex-direction: column;
    text-align: left;
}

.sidebar-wrap {
    width: 20%;
}

.side-info p {
    margin: .5rem;
}

.side-info li {
    padding: 0 0 .4rem .3rem;
    list-style-type: square;
    font-size: 3vh;
}

.about-title-p {
    margin: 0;
    padding: 0;
    font-weight: 100;
    font-size: 5vh;
    font-family: 'Yeseva One';
}

.sidebar-title {
    margin: .6rem 0;
    padding: 0;
    text-align: left;
    color: #828282;
    font-size: 3vh;
    overflow-wrap: break-word;
}

.work-p {
    text-align: left;
    font-size: 3vh;
}

.launch {
    background-color: #4155D1;
    width: auto;
    height: auto;
    font-size: 1.2rem;
    padding: .5rem;
    cursor: pointer;
}

.launch a {
    background-color: #4155D1;
    color: #F5F6F0;
}

video {
    margin: 5rem;
}

.head-photo {
    width: 60%;
    height: auto;
    margin: 1rem;
}


.about-title {
    text-align: center;
    font-size: 5vh;
}

.about-wrap {
    margin: 8vh;
}

.headshot-paragraph-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 0;
}

.headshot {
    background-image: url(/static/media/Bb_Headshot-7612.d5c78fc5.png);
    background-size: contain;
    background-repeat: no-repeat;
    width: 170vh;
    height: 40vh;
    margin: 1rem;
    padding-left: 5rem;
}

.blob {
    display: flex;
    align-self: center;
    background-image: url(/static/media/blob-shape.8d766a1d.svg);
    background-size: cover;
    z-index: -10;
    width: 100%;
    padding: 0;

}

.paragraph-icon-wrap {
    margin: 2rem 2rem 0 2rem;
}

.about-paragraph {
    flex-wrap: wrap;
    margin: 1rem 0 2.5rem 0;
    padding: 0;
    font-size: 3vh;
    font-family: 'Josefin Sans';
    text-align: center;
}

.web-link {
    text-decoration: underline;
    font-weight: 500;
}

.icon-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 5rem;
}

.react-icons {
    width: 7vh;
    height: 7vh;
}

.react-icons:hover, .web-link:hover  {
    opacity: 50%;
}
* {
  box-sizing: border-box;
}

h1 {
  font-family: 'Yeseva One', cursive;
}

.page-titles {
  font-size: 4vh;
  text-align: center;
  margin-top: 4vh;
}

